#welcome_home {
  font-weight: bolder;
  font-family: 'Open Sans', sans-serif;
  background-color: #ddd !important;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-position: center center;
  overflow-x: hidden;
  margin-top: 0px;
;

  padding: 15%;
